.ready {
  padding: 60px 15px;

  &__container {
    background: rgba(129, 129, 139, 0.1);
    border-radius: 30px;
    padding: 52px 5px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    gap: 50px;
  }

  &__content {
    max-width: 374px;
  }

  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 32px;
    line-height: 45px;
    text-align: center;
    color: var(--white);
    margin-bottom: 16px;

    @include mobile {
      font-size: 25px;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: var(--gray);

    @include mobile {
      font-size: 14px;
    }
  }

  &__btn {
    display: flex;
    gap: 32px;

    @include mobile {
      gap: 15px;
    }
  }
}
