.nav {

  &__list {
    display: flex;
    gap: 35px;

    @include tablet {
      flex-direction: column;
    }
  }

  &__link {
    text-decoration: none;
    font-weight: 300;
    font-size: 16px;
    line-height: 26px;
    color: var(--white);
    transition: 0.5s;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      bottom: -3px;
      left: 0;
      width: 0%;
      height: 1px;
      background: var(--white);
      transition: 0.5s;
    }

    &:hover {
      color: var(--blue);

      &::before {
        width: 100%;
      }
    }
  }

  @include tablet {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    background-color: #0D0D11;
    display: flex;
    align-items: center;
    padding: 0 80px 0 50px;
    transform: translateX(-150%);
    opacity: 0;
    transition: 0.5s;
    z-index: 5;
  }
}

.menu--active {
  transform: translateX(0);
  opacity: 1;
}
