.footer {
  padding: 60px 0;

  &__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 64px;
  }

  &__col {
    max-width: 378px;
  }

  &__logo {
    display: inline-block;
    margin-bottom: 32px;
  }

  &__info {
    margin: 0;
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    color: var(--gray);
    margin-bottom: 33px;
  }

  &__social {
    display: flex;
    gap: 15px;
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: var(--white);
    margin-bottom: 30px;
  }

  &__menu {
    display: flex;
    flex-direction: column;
    gap: 25px;
  }

  &__link {
    font-weight: 300;
    font-size: 16px;
    line-height: 26px;
    color: var(--gray);
    transition: .4s;

    &:hover {
      color: var(--white);
    }
  }
}

.form {
  display: inline-flex;
  align-items: center;
  gap: 16px;

  &__label {
    width: 100%;
  }

  &__input {
    width: 100%;
    padding: 14px 33px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--gray);
    border: 1px solid rgba(129, 129, 139, 0.3);
    border-radius: 50px;
    background: transparent;
  }
}
