.services {
  padding: 60px 0;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;


  }

  &__content {
    flex: 0 1 512px;

    @include tablet {
      margin-bottom: 50px;
      flex: 0 1 700px;
      justify-content: flex-start;
    }
  }

  &__header {
    margin-bottom: 19px;
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 51px;
    line-height: 61px;
    color: var(--white);
    margin-bottom: 12px;
  }

  &__descr {
    margin: 0;
    font-weight: 300;
    font-size: 18px;
    line-height: 27px;
    color: var(--gray);
    margin-bottom: 33px;
  }

  &__card {
    display: flex;
    gap: 32px;

    @include tablet {
      width: 100%;
      justify-content: center;
    }

    @include small-tablet {
      flex-wrap: wrap;
    }
  }

  &__row-1 {
    display: flex;
    flex-direction: column;
    transform: translateY(32px);

    :first-child {
      margin-bottom: 32px;
    }

    @include small-tablet {
      transform: translateY(0);
    }
  }

  &__row-2 {
    display: flex;
    flex-direction: column;

    :first-child {
      margin-bottom: 32px;
    }
  }

  &__card-item {
    max-width: 315px;
    padding: 45px 25px 50px 25px;
    background: rgba(129, 129, 139, 0.1);
    border-radius: 30px;
  }

  &__card-img {
    margin-bottom: 16px;
  }

  &__card-title {
    margin: 0;
    font-weight: 400;
    font-size: 19px;
    line-height: 27px;
    color: var(--white);
    margin-bottom: 10px;
  }

  &__card-descr {
    margin: 0;
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    color: var(--gray);
  }

}
