.hero {
  padding: 45px 0;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;

    @include tablet {
      flex-direction: column;
      justify-content: center;
    }
  }

  &__content {
    max-width: 510px;

    @include tablet {
      max-width: 810px;
      text-align: center;
    }
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 51px;
    line-height: 61px;
    color: var(--white);
    margin-bottom: 28px;

    @include mobile {
      font-size: 29px;
      line-height: 50px;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 300;
    font-size: 18px;
    line-height: 27px;
    color: var(--gray);
    margin-bottom: 33px;

    @include mobile {
      font-size: 14px;
      line-height: 25px;
    }
  }

  &__btn {
    display: flex;
    gap: 32px;

    @include tablet {
      justify-content: center;
    }
  }
}
