.testimonials {
  padding: 60px 0;

  &__content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 30px;
    margin-bottom: 65px;
  }

  &__info {
    max-width: 768px;
  }

  &__header {
    margin-bottom: 19px;
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 51px;
    line-height: 61px;
    color: var(--white);
    margin-bottom: 12px;

    @include mobile {
      font-size: 39px;
    }
  }

  &__descr {
    font-weight: 300;
    font-size: 18px;
    line-height: 27px;
    color: var(--gray);

    @include mobile {
      font-size: 15px;
    }
  }

  &__btn {
    align-self: flex-end;
  }

  &__card {
    display: flex;
    justify-content: center;
    gap: 48px;
    flex-wrap: wrap;
  }

  &__item {
    max-width: 394px;
    background: rgba(129, 129, 139, 0.1);
    border-radius: 30px;
    padding: 64px 40px;
  }

  &__card-header {
    margin-bottom: 33px;
  }

  &__card-info {
    margin: 0;
    font-weight: 400;
    font-size: 32px;
    line-height: 45px;
    color: var(--white);
    margin-bottom: 33px;
  }

  &__card-text {
    margin: 0;
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    color: var(--gray);
    margin-bottom: 35px;
  }

  &__card-rating {
    display: flex;
    gap: 5px;
  }
}

.card-user {
  display: flex;
  gap: 16px;

  &__name {
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    color: var(--white);
    margin-bottom: 7px;
  }

  &__descr {
    font-weight: 300;
    font-size: 14px;
    line-height: 22px;
    color: var(--gray);
  }
}
