.choose {
  padding: 60px 0;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    flex-wrap: wrap;

    @include tablet {
      align-items: start;
      flex-direction: column-reverse;
    }
  }

  &__content {
    max-width: 610px;

    @include tablet {
      max-width: 750px;
    }
  }

  &__header {
    margin-bottom: 43px;
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 51px;
    line-height: 61px;
    color: var(--white);
    margin-bottom: 40px;

    @include mobile {
      font-size: 29px;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 300;
    font-size: 18px;
    line-height: 27px;
    color: var(--gray);
    margin-bottom: 35px;

    @include mobile {
      font-size: 15px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__list-item {
    display: flex;
    align-items: center;
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    color: var(--gray);

    &::before {
      content: "";
      display: block;
      background: url(../img/check.svg);
      width: 32px;
      height: 32px;
      margin-right: 10px;
    }
  }

  &__img {
    @include tablet {
      width: 100%;
      text-align: center;
    }
  }
}
