.blog {
  padding: 60px 0;

  &__content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 30px;
    margin-bottom: 65px;
  }

  &__info {
    max-width: 768px;
  }

  &__header {
    margin-bottom: 19px;
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 51px;
    line-height: 61px;
    color: var(--white);
    margin-bottom: 12px;

    @include mobile {
      font-size: 39px;
    }
  }

  &__descr {
    font-weight: 300;
    font-size: 18px;
    line-height: 27px;
    color: var(--gray);

    @include mobile {
      font-size: 15px;
    }
  }

  &__btn {
    align-self: flex-end;
  }


  &__card {
    display: flex;
    justify-content: center;
    gap: 48px;
    flex-wrap: wrap;
  }

  &__card-item {
    max-width: 394px;
    background: rgba(129, 129, 139, 0.1);
    border-radius: 24px;
  }

  &__card-header {
    position: relative;
  }

  &__card-img {
    vertical-align: bottom;
    border-radius: 24px 24px 0px 0px;
  }

  &__card-info {
    display: inline-block;
    padding: 9px 10px;
    position: absolute;
    bottom: 0;
    left: 0;
    background: rgba(129, 129, 139, 0.5);
    border-radius: 0px 10px 0px 0px;
    font-weight: 300;
    font-size: 14px;
    line-height: 22px;
    color: var(--white);
  }

  &__card-content {
    padding: 36px 28px 40px 26px;
  }

  &__card-data {
    display: inline-block;
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    color: var(--gray);
    margin-bottom: 20px;
  }

  &__card-title {
    margin: 0;
    font-weight: 400;
    font-size: 19px;
    line-height: 27px;
    color: var(--white);
    margin-bottom: 30px;
  }

  &__card-descr {
    margin: 0;
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    color: var(--gray);
    margin-bottom: 20px;
  }

  &__card-link {
    font-weight: 300;
    font-size: 14px;
    line-height: 22px;
    color: var(--blue);

    &:hover {
      text-decoration: underline;
    }
  }
}
