.partners {
  padding: 50px 0;

  &__title {
    margin: 0;
    text-align: center;
    font-weight: 300;
    font-size: 14px;
    line-height: 22px;
    color: var(--white);
    margin-bottom: 25px;
  }

  .swiper-slide {
    display: flex;
    justify-content: center;
    position: relative;

    &:last-child {
      &::after {
        content: "";
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        width: 40%;
        background: linear-gradient(270deg, #0D0D11 8%, rgba(0, 0, 0, 0) 100%);
        display: block;

        @include tablet {
          display: none;
        }
      }
    }

    &:first-child {
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 30%;
        background: linear-gradient(270deg, #0D0D11 8%, rgba(0, 0, 0, 0) 100%);
        display: block;

        @include tablet {
          display: none;
        }
      }
    }
  }
}
