.cart {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-left: 27px;
  transition: all 0.3s;
  z-index: 10;

  @include mobile {
    margin-left: 0;
  }

  &:hover {
    .cart__num {
      opacity: 1;
    }
  }

  &__text {
    font-weight: 300;
    font-size: 17px;
    line-height: 28px;
    color: var(--white);
    transition: 0.3s;
    cursor: pointer;

    @include mobile {
      font-size: 14px;
    }

    &:hover {
      color: var(--blue);
    }
  }

  &__num {
    display: block;
    padding: 10px 10px;
    background: rgba(129, 129, 139, 0.3);
    border-radius: 29.9375px;
    font-weight: 500;
    font-size: 14px;
    color: var(--white);
    opacity: 0;
    transition: all 0.3s;

    &:hover {
      opacity: 1;
    }
  }
}
