.description {
  padding: 60px 0;

  &__container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
    max-width: 750px;
  }

  &__item {
    flex: 1 1 358px;
    padding: 40px;
    background: rgba(129, 129, 139, 0.1);
    border-radius: 30px;
  }

  &__info {
    font-weight: 500;
    font-size: 51px;
    line-height: 61px;
    color: var(--white);
  }

  &__text {
    font-weight: 300;
    font-size: 18px;
    line-height: 27px;
    color: var(--gray);
  }

  &__img {}
}
