// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// Глобальные стили
.btn {
  padding: 16px 27px;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  color: var(--white);
  background: var(--blue);
  border-radius: 50px;

  @include mobile {
    font-size: 12px;
  }

  &:hover {
    transition: .3s;
    background: var(--blue);
    opacity: 0.4;
  }
}

.btn-secondary {
  padding: 16px 27px;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  color: var(--white);
  background: rgba(129, 129, 139, 0.2);
  border-radius: 50px;
  transition: .3s;

  @include mobile {
    font-size: 12px;
  }

  &:hover {
    opacity: 0.4;
  }
}

.header-sections {
  display: inline-block;
  border: 1px solid rgba(129, 129, 139, 0.2);
  border-radius: 12px;
  padding: 11px 19px;
  background: transparent;
  font-weight: 300;
  font-size: 11px;
  line-height: 17px;
  color: var(--gray);
}

.logo {
  z-index: 10;
  min-width: 120px;
}

