.header {
  padding: 32px 0;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
  }

  &__row {
    display: flex;
    align-items: center;

  }

  &__btn {
    margin-left: 32px;

    @include mobile {
      display: none;
    }
  }
}

.burger {
  display: none;
  @include burger;
  z-index: 10;

  @include tablet {
    display: block;
    color: var(--white);
  }
}
